import {TouchableOpacity, useWindowDimensions, View} from 'react-native';
import WebView from 'react-native-webview';
import {IWidgetDetail} from '../../../../../model/interface/workspace.interface';
import {Icon} from '@ui-kitten/components';
import TextCM from '../../../../components/Text';
import {getConfig} from '../../../../../services/httpClient';
import {styles} from './styles';
import {TYPE_SCREEN} from '../../../../../constants/Size';

type Props = {
  widget: IWidgetDetail;
  handleSelectWidget: (widget: IWidgetDetail) => void;
};

const PreviewWidget = ({widget, handleSelectWidget}: Props) => {
  const {width} = useWindowDimensions();
  return (
    <View style={styles.ctn}>
      <View
        style={[
          styles.header,
          widget.serviceName === 'eAnalysis' && {marginBottom: 10},
        ]}>
        <TextCM
          style={
            styles.txtHeader
          }>{`${widget.widgetName} (${widget.serviceName})`}</TextCM>
        <TouchableOpacity
          hitSlop={15}
          onPress={() => handleSelectWidget(widget)}>
          <Icon
            name="eye-outline"
            width={20}
            height={20}
            fill="rgba(0, 32, 77, 0.9)"
          />
        </TouchableOpacity>
      </View>
      {widget.serviceName === 'eAnalysis' ? (
        <WebView
          style={{
            height:
              TYPE_SCREEN.mobile > width
                ? 270
                : JSON.parse(widget?.config || '{}')?.h * 62.5,
            width: '100%',
          }}
          source={{
            uri: `${
              getConfig().BASE_URL_EANALYSIS
            }superset_app/superset/dashboard/${
              JSON.parse(widget.param || '{}')?.reportSelect
            }/?standalone=true&${
              JSON.parse(widget.param || '{}')?.configAdvanced?.darkMode
                ? 'theme=dark'
                : 'theme=light'
            }`,
          }}
          startInLoadingState
          sharedCookiesEnabled
          thirdPartyCookiesEnabled
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
        />
      ) : (
        <></>
      )}
    </View>
  );
};

export default PreviewWidget;
